<template>
	<div class="main">
		
		<div class="local">
			<span class="pos1"></span> > <span class="pos2">{{ $route.name}}</span>
		</div>
		<div class="search">
			    <div class="del" @click="openDel"><span></span></div>
			    <div class="update" @click="updateMap"><span></span></div>
			    <div class="add" @click="addMap"><span></span></div>
		</div>
			<div class="page_list">
				<div class="table">
					<table class="msg_table">
					            				<thead>
					            					<tr>
					            						<td>
					            							<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
					            							</div>
					            						</td>
					            						<td v-for="i in thead">{{i}}</td>
					            					</tr>
					            				</thead>
					            				<tbody :view-id="changeP">
					            					<ttboady @mapService="mapS" @menuManage="menuM" @sendTodo="getTodo" class="ManageRole" v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="(item,index) in tr" v-bind:todo="item" v-bind:index="index" v-bind:key="item.id"></ttboady>
					            				</tbody>
					            		</table>
				</div>
		
			        <ul class="page">
			            <li><span class="total_page">{{totalPage}}</span>
			            	<span class="page_num">{{pageSize}}</span>
			            	<span class="total_num">{{totalNum}}</span></li>
			            <li><div class="top" @click="firstPage"></div></li>
			            <li><div class="prev" @click="prevPage"></div></li>
			            <li><input type="text" v-model="pageIndex" class="local_page"/></li>
			           	<li><div class="next" @click="nextPage"></div></li>
			            <li><div class="bottom" @click="lastPage"></div></li>
			            <li><div class="gon" @click="GOPage">GO</div></li>
			        </ul>
			</div>
			<!--modal-->
			<Modal v-model="rolemodal">
		        <div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		        </div>
					<div class="eltree">
						<el-tree
						  :data="roleM"
						  show-checkbox
						  node-key="id"
						  :default-expanded-keys="[2, 3]"
						  :default-checked-keys="[5]"
						  @check-change="checkTree"
						  :props="defaultProps">
						</el-tree>
					</div>
		   		  <div slot="footer">
			        	<Button type="text"  @click="rolemodal = false"></Button>
			            <Button type="primary"  @click="rolemodal = true"></Button>
		        	</div>
		   </Modal>
		   
		   
		    <Modal v-model="mapService">
		    	<div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		        </div>
		    	
		        <div class="eltree">
					<el-tree
						  :data="roleM"
						  show-checkbox
						  node-key="id"
						  :default-expanded-keys="[2, 3]"
						  :default-checked-keys="[5]"
						  :props="defaultProps">
					</el-tree>
				</div>
				<div slot="footer">
			        	<Button type="text"  @click="mapService = false"></Button>
			            <Button type="primary"  @click="mapService = true"></Button>
		        	</div>
		    </Modal>

			<Modal v-model="modaldel" >
		        
		        <div slot="header" style="color:#f60;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		        </div>
		        
		        <p></p>
		        <div slot="footer">
		        	<Button type="primary"  @click="closeModal"></Button>
		            <Button type="error"  @click="delMap"></Button>
		        </div>
		    </Modal>
		    <Modal
		        v-model="modalTotal" @on-cancel="closeModal">
		         <div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		        </div>
		         <Form ref="formInline" :model="roleMsg" :rules="ruleInline" :label-width="140">
		         	<FormItem prop="ROLE_MC" label="">
			            <Input type="text" v-model="roleMsg.ROLE_MC" placeholder="">
			            </Input>
			        </FormItem>
			        <FormItem prop="MIAOSHU" label="">
			            <Input type="text" v-model="roleMsg.MIAOSHU" placeholder="">
			            </Input>
			        </FormItem>
		         	 <FormItem prop="CHUANGJIAN_DATE" label="">
						 
						<el-date-picker
					      v-model="roleMsg.CHUANGJIAN_DATE"
					      type="datetime"
					      style="width:350px"
					      placeholder="">
					    </el-date-picker>

			        </FormItem>
		   		 </Form>
		   		  <div slot="footer">
			        	<Button type="text" @click="closeModal"></Button>
			            <Button type="primary" @click="handleSubmit('formInline')"></Button>
		        	</div>
		    </Modal>
			
	</div>

</template>

<script>
 import ttboady from './page/comments/table-tbody'
 import config from '../config/config.js'
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["","","",""],
		    					tr:[], 
		    					isA:false,
		    					isfA:false,
		    					modaldel:false,
		    					modalTotal:false,
		    					pageIndex:1,
		    					pageSize:12,
		    					totalPage:null,
		    					totalNum:null,
		    					keyword:null,
		    					submitType:null,
		    					roleMsg: {
				                    MIAOSHU:null,
				                    ROLE_MC:null,
				                    CHUANGJIAN_DATE:'',
				               },
				                
				                canEdit:true,
				                rolemodal:false,
				                mapService:false,
				                canEdit:true,
				                
				                updateMsg:{},
				                ruleInline: {
					                    MIAOSHU: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                    ROLE_MC: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],
				                },
				                
				                 roleM: [{
								          id: 1,
								          label: ' 1',
								          children: [{
								            id: 4,
								            label: ' 1-1',
								            children: [{
								              id: 9,
								              label: ' 1-1-1'
								            }, {
								              id: 10,
								              label: ' 1-1-2'
								            }]
								          }]
								        }, {
								          id: 2,
								          label: ' 2',
								          children: [{
								            id: 5,
								            label: ' 2-1'
								          }, {
								            id: 6,
								            label: ' 2-2'
								          }]
								        }, {
								          id: 3,
								          label: ' 3',
								          children: [{
								            id: 7,
								            label: ' 3-1'
								          }, {
								            id: 8,
								            label: ' 3-2'
								          }]
								        }],
								        defaultProps: {
								          children: 'children',
								          label: 'label'
								        }
		    					
								}
		    },
		    	computed:{
		    		changeP:function(){
		    			console.log(this.$store.state.placeId);
		    			this.$store.state.placeId="1101001"
						this.reloadTable();
		    			return this.$store.state.placeId;
		    		},
		    		
		    	},
		    	updated(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	
		    	methods:{
		    		
		    		mapS:function(data){
		    			this.mapService=true;
		    			console.log(data);
		    		},
		    		menuM:function(data){
		    			this.rolemodal=true;
		    			console.log(data);
		    		},
		    		check_box:function(){
		    			this.isA = !th1is.isA;
		    			this.isfA = !this.isfA;
		    			if(this.isA){
		    				this.$store.state.tableselectCode=this.$store.state.tableallCode;
		    			}else{
		    				this.$store.state.tableselectCode=[];
		    			}
		    			console.log(this.$store.state.tableselectCode);
		    		},
		    		searchMsg(){
		    			this.reloadTable();
		    		},
		    		openDel(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    			}else{
		    				this.modaldel = true;
		    			}
		    		},
		    		delMap(){
						this.modaldel = false;
						
		    			this.$http.get(config.content+'/asmx/RoleService.asmx/DeleteRoleByID ',{params:{key:this.$store.state.tableselectCode.join(",")}}).then(response => {				
										this.$Notice.success({
						                    title: '',
						               });
										this.reloadTable();
						        }, response => {
						             swal("", "", "error");
						        });
		    		},
		    		reizeDate(data){
		    			console.log(data);
		    			const d=new Date(data);
		    			let h= d.getHours()<10? "0"+d.getHours() : d.getHours();
		    			let m= d.getMinutes()<10? "0"+d.getMinutes() : d.getMinutes();
		    			let s= d.getSeconds()<10? "0"+d.getSeconds() : d.getSeconds();
		    			return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + h + ':' + m + ':' + s;  
		    		},
		    		 handleSubmit(name) {
		                this.$refs[name].validate((valid) => {
		                    if (valid) {
		                    	let jsons=this.roleMsg;
		                    	jsons.PARENTID=this.$store.state.placeId;
		                    	
		                    	jsons.XIUGAI_DATE=this.reizeDate(jsons.XIUGAI_DATE)
		                    	jsons.CHUANGJIAN_DATE=this.reizeDate(jsons.CHUANGJIAN_DATE)
		                    	
		                    	
		                    	jsons=JSON.stringify(jsons);
		                    	console.log(jsons);
		                    	
		                    	if(this.submitType =="add"){
		                    		this.$http.get(config.content+'/asmx/RoleService.asmx/AddRole',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										
										this.$Message.success('');
			                        	this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							             swal("", "", "error");
							        });
		                    	}else{
		                    		this.$http.get(config.content+'/asmx/RoleService.asmx/UpdateRole',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										   this.$Message.success('');
			                        	   this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							    
							            swal("", "", "error");
							        });
		                    	}
		                    	
		                    	
		                    } else {
		                        this.$Message.error('');
		                    }
		                })
		            },
		    		closeModal(){
		    			this.modalTotal = false;
		    				this.modaldel = false;
		    				
		    				this.$Message.info('');
		    				this.reloadTable();
		    		},
		    		getTodo(data){
		    			this.updateMsg=data;
		    		},
		    		addMap(){
		    			this.submitType ="add";
		    			this.roleMsg={
				                    CHUANGJIAN_DATE:'',
				                    MIAOSHU:null,
				                    ROLE_MC:null,
				                },
		    			this.modalTotal=true;
		    		},
		    		updateMap(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    				//swal("", "", "warning");
		    			}else{
		    				this.submitType ="update";
			    			this.roleMsg=this.updateMsg;
			    			
			    			console.log(this.roleMsg);
			    			
			    			this.modalTotal=true;
		    			}
		    			
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		},
		    		
		    		reloadTable(){
		    					console.log(this.keyword);
		    			let Index=this.pageIndex-1;
		    			this.$http.get(config.content+'/asmx/RoleService.asmx/GetRoleDatataByPage',{params:{PARENTID:this.$store.state.placeId,pageSize:this.pageSize,pageIndex:Index,keyword:this.keyword}}).then(response => {
						 								
										let result = response.body;
						                let father=[];
						                 result = $.parseXML(result);
						                 let obj;
						                 $(result).find("string").each(function(i){                     
						                 	obj=$.parseJSON($(this).text());                      
						                })
						                 
						                 console.log(obj);
						                 this.tr=obj.Result.TableInfo;
						                 this.$store.state.tableallCode=[];
						                 this.$store.state.tableselectCode=[];
						                 for(var i=0;i<this.tr.length;i++){
						                 	this.$store.state.tableallCode.push(this.tr[i].ID);
						            		this.tr[i].cancel="0";
						                 }
						                 
						                 	this.totalNum=obj.Result.RCount;
						                 	this.totalPage=obj.Result.PCount;
										
						        }, response => {
						            console.log("error");
						        });
		    		},
		    		//checktree
		    		checkTree(a,b,c){
		    			console.log("----------");
		    			console.log(a);
		    			console.log(b);
		    			console.log(c);
		    		},
		    		
		    		
		    		//
		    		nextPage(){
		    			if( this.pageIndex>this.totalPage-1){
		    				
		    				this.$Message.error('');
		    				this.pageIndex=this.totalPage;
		    			}else{
		    				this.pageIndex++;
		    				this.reloadTable();
		    			}
		    		},
		    		prevPage(){
		    			
		    			if( this.pageIndex<2){
		    				this.$Message.error('');
		    				this.pageIndex=1;
		    			}else{
		    				this.pageIndex--;
		    				this.reloadTable();
		    			}
		    		},
		    		firstPage(){
		    			this.pageIndex=1;
		    			this.reloadTable();
		    		},
		    		lastPage(){
		    			this.pageIndex=this.totalPage;
		    			this.reloadTable();
		    		},
		    		GOPage(){
		    			this.reloadTable();
		    		}
		    	},
}
</script>

<style scoped="scoped">
	

/********************/
.main .table{
	margin-bottom:0px;
}
.main div.search{
	color:#fff;
	height:2.5em;
	padding-right:0.5em;
}
.main div.search>div{
	color:#fff;
	float:right;
	width:4.5em;
	height:2em;
	border-radius: 5px;
	background-color: rgba(205,210,234,0.2);
	margin-left:10px;
	line-height: 2em;
	cursor: pointer;
	border:1px solid #8a92a8;
}
.main div.search>div.input{
	width:18em;
	position:relative;
	border:none;
	background: none;
	margin-top:-2px;
}
.main div.search>div.input>span{
	width:5em;
	display: inline-block;
}
/*.main div.search input{
	width:22.6em;
	outline: none;
	background: none;
	border:none;
	color:#fff;
	padding-left:1em;
}
.main div.search div#icon{
	position:absolute;
	height:2em;
	width:2em;
	background-image: url(../assets/Manage/search.png);
	background-size: contain;
	top:0px;
	right:0px;
}
.main div.search div#icon:hover{
	top:2px;
}*/

.main div.search input::-webkit-input-placeholder {
  color: #fff;
}
.main div.search input:-moz-placeholder {/* Firefox 18- */
  color: #fff;
}
.main div.search input::-moz-placeholder{/* Firefox 19+ */
 color: #fff;
}
.main div.search input:-ms-input-placeholder {
  color: #fff;
}
.main div.search input:-o-input-placeholder {
  color: #fff;
}
.main div.search div>span{
	display: block;
	float:left;
	height:22px;
	width:22px;
	margin-top:5px;
	margin-left:5px;
}
.main div.search div.del:hover{
	padding-top:2px;
}
.main div.search div.add:hover{
	padding-top:2px;
}
.main div.search div.update:hover{
	padding-top:2px;
}
.main div.search div.del>span{
	background-image: url(../assets/Manage/del.png);
}
.main div.search div.add>span{
	background-image: url(../assets/Manage/add.png);
}
.main div.search div.update>span{
	background-image: url(../assets/Manage/update.png);
}
/************************/
.main div.table{
	height: 600px;
}
.main table.msg_table tr td:nth-child(9){
	display:none;
}
</style>


