<template>
	<div class="main">
		
		<div class="local">
		<span class="pos1"></span> > <span class="pos2">{{ $route.name}}</span>
	</div>
	<div class="search">
		    <div class="del" @click="openDel"><span></span></div>
		    <div class="update" @click="updateMap"><span></span></div>
		    <div class="add" @click="addMap"><span></span></div>
		    <div class="input">
			    <span></span>
				<Select class="input" v-model="keyword" @change="searchMsg" style="width:200px;background:none">
					  <Option value=""></Option>
					 <Option value="B">B/S</Option>
                      <Option value="C">C/S</Option>
	    		</Select>
			</div>
	</div>
	<Modal v-model="modaldel" >
         <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span></span>
        </p>
        <p></p>
        <div slot="footer">
        	<Button type="primary"  @click="closeModal"></Button>
            <Button type="error"  @click="delMap"></Button>
        </div>
    </Modal>
    <Modal
        v-model="modalTotal" @on-cancel="closeModal">
        <div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		 </div>
         <Form ref="formInline" :model="OnlineUpdateMsg" :rules="ruleInline" :label-width="140">
         	<FormItem prop="BANBEN" label="">
	            <Input type="text" v-model="OnlineUpdateMsg.BANBEN" placeholder="">
	            </Input>
	        </FormItem>
	        <FormItem label="" prop="SYS_LX">
	        	<Select v-model="OnlineUpdateMsg.SYS_LX" placeholder="">
	                  <Option value="B">B/S</Option>
                      <Option value="C">C/S</Option>
                </Select>
	        </FormItem>
	        <FormItem prop="LUJING" label="">
	            <Input type="text" v-model="OnlineUpdateMsg.LUJING" placeholder="">
	            </Input>
	        </FormItem>
         	 <FormItem prop="GENGXIN_NR" label="">
	            <Input type="text" v-model="OnlineUpdateMsg.GENGXIN_NR" placeholder="">
	            </Input>
	        </FormItem>
	      	<FormItem prop="TIJIAO_RY" label="">
	            <Input type="text" v-model="OnlineUpdateMsg.TIJIAO_RY" placeholder="">
	            </Input>
	        </FormItem>
	       <FormItem prop="GENGXIN_SJ" label="">
	       		<el-date-picker
					      v-model="OnlineUpdateMsg.GENGXIN_SJ"
					      type="datetime"
					      style="width:348px"
					      placeholder="">
				</el-date-picker>
	       </FormItem>
	       
	       <FormItem prop="Upload">
                <Upload 
                	action="http://101.200.232.210:8020/asmx/OnlineUpdateService.asmx/UploadFile"
              
                	:format="['apk']"
                	:on-format-error="handleFormatError"
               		:max-size = "1024000"
                	:on-success="Uploadsuccess"
                	:show-upload-list= "true"
                	:on-error="Uploaderror">
        			<Button type="ghost" icon="ios-cloud-upload-outline"></Button>
    			</Upload>
				<div v-if="file !== null" >{{ file.name }} <Button type="text" @click="uploadA" :loading="loadingStatus">{{ loadingStatus ? '' : '' }}</Button></div>
	        </FormItem>
	       
   		 </Form>
   		  <div slot="footer">
	        	<Button type="text" @click="closeModal"></Button>
	        	<Button type="primary" @click="handleSubmit('formInline')"></Button>
          </div>
    </Modal>
	<div class="page_list">
		<div class="table">
			<table class="msg_table">
			            				<thead>
			            					<tr>
			            						<td>
			            							<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
			            							</div>
			            						</td>
			            						<td v-for="i in thead">{{i}}</td>
			            					</tr>
			            				</thead>
			            				<tbody :view-id="changeP">
			            					<ttboady @sendTodo="getTodo" class="OnlineUpdate" v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="(item,index) in tr" v-bind:todo="item" v-bind:index="index" v-bind:key="item.id"></ttboady>
			            				</tbody>
			            		</table>
		</div>
		
			            				<ul class="page">
			            					<li><span class="total_page">{{totalPage}}</span>
			            						<span class="page_num">{{pageSize}}</span>
			            						<span class="total_num">{{totalNum}}</span>
			            					</li>
			            					<li>
			            						<div class="top" @click="firstPage"></div>
			            					</li>
			            					<li><div class="prev" @click="prevPage"></div></li>
			            					<li>
			            						<input type="text" v-model="pageIndex" class="local_page"/>
			            					</li>
			            					<li><div class="next" @click="nextPage"></div></li>
			            					<li><div class="bottom" @click="lastPage"></div></li>
			            					<li><div class="gon" @click="GOPage">GO</div></li>
			            				</ul></div>
	</div>
</template>

<script>
 import ttboady from './page/comments/table-tbody'
 import config from '../config/config.js'
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["","","","","","",],
		    					tr:[
									
									],         
		    					isA:false,
		    					isfA:false,
		    					modaldel:false,
		    					modalTotal:false,
		    					pageIndex:1,
		    					pageSize:12,
		    					totalPage:null,
		    					totalNum:null,
		    					keyword:null,
		    					submitType:null,
		    					file:null,
		    					loadingStatus:false,
		    					OnlineUpdateMsg: {
				                    BANBEN:null,
				                    GENGXIN_NR:null,
				                    LUJING:null,
				                    SYS_LX:null,
				                    TIJIAO_RY:null,
				                    GENGXIN_SJ:null,
				                },
				                SYS_LX:{
				                	B:"B/S",
				                	C:"C/S",
				                },
				                updateMsg:{},
				                ruleInline: {
					                    BANBEN: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                    GENGXIN_NR: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                    SYS_LX: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],
					                    LUJING: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],
					                    TIJIAO_RY: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ]
				                	}
		    					
								}
		    },
		    	computed:{
		    		changeP:function(){
		    			console.log(this.$store.state.placeId);
		    			this.$store.state.placeId="1101001"
						this.reloadTable();
		    			return this.$store.state.placeId;
		    		},
		    	},
		    	updated(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");

		    	},
		    	methods:{
		    		check_box:function(){
		    			this.isA = !this.isA;
		    			this.isfA = !this.isfA;
		    			if(this.isA){
		    				this.$store.state.tableselectCode=this.$store.state.tableallCode;
		    			}else{
		    				this.$store.state.tableselectCode=[];
		    			}
		    			console.log(this.$store.state.tableselectCode);
		    		},
		    		handleUpload (file) {
		                this.file = file;
		                return false;
		            },
		            uploadA () {
		                this.loadingStatus = true;
		                console.log(this.file);
		                if( this.file.length<1){
        					this.$Message.error('');
        				}else{

        				/*	setTimeout(() => {
				                    this.file = null;
				                    this.loadingStatus = false;
				                    this.$Message.success('')
				            }, 1500);*/
        					
        					 this.loadingStatus = false;
        					 this.hanleUpload();
        					}
		            },
		    		Upload(){
		    			 var fd = new FormData();
        				fd.append("fileToUpload", document.getElementById('UploadFile').files[0]);
        				
        				if( document.getElementById('UploadFile').files.length<1){
        					this.$Message.error('');
        				}else{
        						$.ajax({
				                    url: config.content+'/asmx/OnlineUpdateService.asmx/UploadFile',
				                    type: 'POST',
				                    data: fd,
				                    async: false,
				                    cache: false,
				                    contentType: false,
				                    processData: false,
		        					success: function (returndata) {
		        					 	console.log(returndata);
								          var  obj;
								          $(returndata).find("string").each(function(i){                     
								             obj=$(this).text();                      
								           })
		        					 	if(obj=="error"){
		        					 		swal({
				                            title: "",
				                            text: "!",
				                            type: "error",
				                            confirmButtonText: "",
				                        	});
		        					 	}else{
		        					 		 swal({
					                            title: "",
					                            text: "",
					                            type: "success",
					                            confirmButtonText: "",
				                        	});
		        					 	}
				                       
				                    },
				                    error: function (returndata) {
				                    	console.log(returndata);
				                        swal({
				                            title: "",
				                            text: "!",
				                            type: "error",
				                            confirmButtonText: "",
				                        });
				                    }
		        				})
        			
        					}
        			
		    		},
		    		Uploadsuccess( response, file, fileList){
		    			let res=$.parseXML(response);
		    			let obj;
		    			$(res).find("string").each(function(i){                     
							obj=$(this).text();                      
						})
		    			if(obj=="error"){
		        					 	swal({
				                            title: "",
				                            text: "!",
				                            type: "error",
				                            confirmButtonText: "",
				                        	});
		        					 	}else{
		        					 		this.$Message.success('');
		        					 	}
		    		},
		    		Uploaderror(response, file, fileList){
		    			swal({
				                            title: "",
				                            text: "!",
				                            type: "error",
				                            confirmButtonText: "",
				                        	});
		    		},
		    		handleFormatError(file){
		    			
		    			this.$Message.error('');
		    			
		    			this.$Notice.warning({
		                    title: '',
		                    desc: ' ' + file.name + ' apk'
		                });
		    		},
		    		searchMsg(){
		    			this.reloadTable();
		    		},
		    		openDel(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    			}else{
		    				this.modaldel = true;
		    			}
		    		},
		    		delMap(){
						this.modaldel = false;
		    			this.$http.get(config.content+'/asmx/OnlineUpdateService.asmx/DelOnlineUpdate',{params:{key:this.$store.state.tableselectCode.join(",")}}).then(response => {				
										this.$Notice.success({
						                    title: '',
						               });
										this.reloadTable();
						        }, response => {
						             swal("", "", "error");
						        });
		    		},
		    		 handleSubmit(name) {
		                this.$refs[name].validate((valid) => {
		                    if (valid) {
		                    	let jsons=this.OnlineUpdateMsg;
		                    	jsons.PARENTID=this.$store.state.placeId;
		                    	
		                    	jsons=JSON.stringify(jsons);
		                    	console.log(jsons);
		                    	
		                    	if(this.submitType =="add"){
		                    		this.$http.get(config.content+'/asmx/OnlineUpdateService.asmx/AddOnlineUpdate',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										
										this.$Message.success('');
			                        	this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							             swal("", "", "error");
							        });
		                    	}else{
		                    		this.$http.get(config.content+'/asmx/OnlineUpdateService.asmx/UpdateOnlineUpdate',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										   this.$Message.success('');
			                        	   this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							    
							            swal("", "", "error");
							        });
		                    	}
		                    	
		                    	
		                    } else {
		                        this.$Message.error('');
		                    }
		                })
		            },
		    		closeModal(){
		    			this.modalTotal = false;
		    				this.modaldel = false;
		    				
		    				this.$Message.info('');
		    				this.reloadTable();
		    		},
		    		getTodo(data){
		    			this.updateMsg=data;
		    		},
		    		addMap(){
		    			this.submitType ="add";
		    			this.OnlineUpdateMsg=
		    			{
				                    BANBEN:null,
				                    GENGXIN_NR:null,
				                    LUJING:null,
				                    SYS_LX:null,
				                    TIJIAO_RY:null,
				                    GENGXIN_SJ:null,
				               };
		    			
		    			this.modalTotal=true;
		    		},
		    		updateMap(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    				//swal("", "", "warning");
		    			}else{
		    				this.submitType ="update";
			    			this.OnlineUpdateMsg=this.updateMsg;
			    			this.modalTotal=true;
		    			}
		    			
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		},
		    		reloadTable(){
		    				
		    			let Index=this.pageIndex-1;
		    			this.$http.get(config.content+'/asmx/OnlineUpdateService.asmx/GetOnlineDataByPage',{params:{PARENTID:this.$store.state.placeId,pageSize:this.pageSize,pageIndex:Index,keyword:this.keyword}}).then(response => {
						 								
										let result = response.body;
						                let father=[];
						                 result = $.parseXML(result);
						                 console.log(result);
						                 let obj;
						                 $(result).find("string").each(function(i){                     
						                 	obj=$.parseJSON($(this).text());                      
						                })

						                 for(var i=0;i<obj.Result.TableInfo.length;i++){
						                 	obj.Result.TableInfo[i].SYS_LX_E=obj.Result.TableInfo[i].SYS_LX;
						                 	
						                	obj.Result.TableInfo[i].SYS_LX=this.SYS_LX[obj.Result.TableInfo[i].SYS_LX[0]];
						                 	
						                 }
						          		console.log(obj.Result.TableInfo);
						                 this.tr=obj.Result.TableInfo;
						                 this.$store.state.tableallCode=[];
						                 this.$store.state.tableselectCode=[];
						                 for(var i=0;i<this.tr.length;i++){
						                 	this.$store.state.tableallCode.push(this.tr[i].ID);
						                 }
						                 
						                 	this.totalNum=obj.Result.RCount;
						                 	this.totalPage=obj.Result.PCount;
										
						        }, response => {
						            console.log("error");
						        });
		    		},
		    		//
		    		nextPage(){
		    			if( this.pageIndex>this.totalPage-1){
		    				
		    				this.$Message.error('');
		    				this.pageIndex=this.totalPage;
		    			}else{
		    				this.pageIndex++;
		    				this.reloadTable();
		    			}
		    		},
		    		prevPage(){
		    			
		    			if( this.pageIndex<2){
		    				this.$Message.error('');
		    				this.pageIndex=1;
		    			}else{
		    				this.pageIndex--;
		    				this.reloadTable();
		    			}
		    		},
		    		firstPage(){
		    			this.pageIndex=1;
		    			this.reloadTable();
		    		},
		    		lastPage(){
		    			this.pageIndex=this.totalPage;
		    			this.reloadTable();
		    		},
		    		GOPage(){
		    			this.reloadTable();
		    		}
		    	},
}
</script>

<style scoped="scoped">
	

/********************/
.main .table{
	margin-bottom:0px;
}
.main div.search{
	color:#fff;
	height:2.5em;
	padding-right:0.5em;
}
.main div.search>div{
	color:#fff;
	float:right;
	width:4.5em;
	height:2em;
	border-radius: 5px;
	background-color: rgba(205,210,234,0.2);
	margin-left:10px;
	line-height: 2em;
	cursor: pointer;
	border:1px solid #8a92a8;
}
.main div.search>div.input{
	width:18em;
	position:relative;
	border:none;
	background: none;
	margin-top:-2px;
}
.main div.search>div.input>span{
	width:5em;
	display: inline-block;
}
/*.main div.search input{
	width:22.6em;
	outline: none;
	background: none;
	border:none;
	color:#fff;
	padding-left:1em;
}
.main div.search div#icon{
	position:absolute;
	height:2em;
	width:2em;
	background-image: url(../assets/Manage/search.png);
	background-size: contain;
	top:0px;
	right:0px;
}
.main div.search div#icon:hover{
	top:2px;
}*/

.main div.search input::-webkit-input-placeholder {
  color: #fff;
}
.main div.search input:-moz-placeholder {/* Firefox 18- */
  color: #fff;
}
.main div.search input::-moz-placeholder{/* Firefox 19+ */
 color: #fff;
}
.main div.search input:-ms-input-placeholder {
  color: #fff;
}
.main div.search input:-o-input-placeholder {
  color: #fff;
}
.main div.search div>span{
	display: block;
	float:left;
	height:22px;
	width:22px;
	margin-top:5px;
	margin-left:5px;
}
.main div.search div.del:hover{
	padding-top:2px;
}
.main div.search div.add:hover{
	padding-top:2px;
}
.main div.search div.update:hover{
	padding-top:2px;
}
.main div.search div.del>span{
	background-image: url(../assets/Manage/del.png);
}
.main div.search div.add>span{
	background-image: url(../assets/Manage/add.png);
}
.main div.search div.update>span{
	background-image: url(../assets/Manage/update.png);
}
/************************/
.main div.table{
	height: 600px;
}
</style>


