<template>
	<div class="main_con">
		
		<div class="local">
			
			<span class="pos1"></span> > <span class="pos2">{{ $route.name}}</span>
		</div>

		<div class="search">
			<div class="date">
				<label for=""></label>
				<el-date-picker  
				   class="reset-el-26 maxw-120"  
				   v-model="staDate"  
				   align="right"  
				   type="date"  
				   placeholder=""  
				   ref="picker1"  
				   :editable="canEdit"  
				   :picker-options="pickerOptions1">  
				 </el-date-picker>
			 <label for=""></label>  
				<el-date-picker  
				  class="reset-el-26 maxw-120"  
				  v-model="endDate"  
				  type="date"  
				  placeholder=""  
				  ref="picker2"  
				  :editable="canEdit"  
				  :picker-options="pickerOptions0">
				</el-date-picker>
			</div>
		    <div class="del"    @click="openDel"><span></span></div>
		    <div class="update" @click="updateNotice"><span></span></div>
		    <div class="add"    @click="addNotice"><span></span></div>
		    <div class="input">
			    <span></span>
				 <Input type="text" v-model="keyword" placeholder="" style="width:180px">
	            </Input>
			</div>
		</div>
	<Modal
        v-model="modaldel">
         <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span></span>
        </p>
        <p></p>
        <div slot="footer">
        	<Button type="primary"  @click="closeModal"></Button>
            <Button type="error"  @click="delNotice"></Button>
        </div>
    </Modal>
    <Modal
        v-model="modalTotal">
         <div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span></span>
		  </div>
         <Form ref="formInline" :model="noticeMsg" :rules="ruleInline" :label-width="100">
         	<FormItem prop="BIAOTI" label="">
	            <Input type="text" v-model="noticeMsg.BIAOTI" placeholder="">
	            </Input>
	        </FormItem>
         	 <FormItem prop="NEIRONG" label="">
	            <Input type="text" v-model="noticeMsg.NEIRONG" placeholder="">
	            </Input>
	        </FormItem>
	        <FormItem prop="FABUREN" label="">
	        	<Input type="text" v-model="noticeMsg.FABUREN" placeholder="">
	           </Input>
	        </FormItem>
	         <FormItem prop="FABU_SJ"  label="">
	         	
	         	<el-date-picker  
				  class="reset-el-26 maxw-120"  
				  v-model="noticeMsg.FABU_SJ"  
				  type="datetime"  
				  placeholder=""  
				  ref="picker3"  
				  :editable="canEdit"  >
				</el-date-picker>
				
	       	</FormItem>
	       
   		 </Form>
   		  <div slot="footer">
	        	<Button type="text" @click="closeModal"></Button>
	            <Button type="primary" @click="handleSubmit('formInline')"></Button>
        	</div>
    </Modal>
	<div class="page_list">
		<div class="table">
			<table class="msg_table">
			            				<thead>
			            					<tr>
			            						<td>
			            							<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
			            							</div>
			            						</td>
			            						<td v-for="i in thead">{{i}}</td>
			            					</tr>
			            				</thead>
			            				<tbody :view-id="changeP">
			            					<ttboady @sendTodo="getTodo"  v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="(item,index) in tr" v-bind:todo="item" v-bind:index="index" v-bind:key="item.id"></ttboady>
			            				</tbody>
			            		</table>
		</div>

			            				<ul class="page">
			            					<li><span class="total_page">{{totalPage}}</span>
			            						<span class="page_num">{{pageSize}}</span>
			            						<span class="total_num">{{totalNum}}</span>
			            					</li>
			            					<li>
			            						<div class="top" @click="firstPage"></div>
			            					</li>
			            					<li><div class="prev" @click="prevPage"></div></li>
			            					<li>
			            						<input type="text" v-model="pageIndex" class="local_page"/>
			            					</li>
			            					<li><div class="next" @click="nextPage"></div></li>
			            					<li><div class="bottom" @click="lastPage"></div></li>
			            					<li><div class="gon" @click="GOPage">GO</div></li>
			            				</ul></div>
	</div>
</template>

<script>
 import ttboady from './page/comments/table-tbody'
 import config from '../config/config.js'
  let vueIns = null  
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["","","",""],
		    					tr:[
									
									],
								pickerOptions0: {  
							        disabledDate(time) {  
							          //   
							          let ayearAgo = Date.now() - 31536000000  
							          return /*time.getTime() > Date.now() || */time.getTime() < ayearAgo  || (vueIns.staDate && vueIns.staDate.getTime && (time.getTime() < vueIns.staDate.getTime()))  
							        }  
							      },  
								pickerOptions1: {  
							        disabledDate(time) {  
							          //   
							          let ayearAgo = Date.now() - 31536000000  
							          return time.getTime() > Date.now() || time.getTime() < ayearAgo || (vueIns.endDate && vueIns.endDate.getTime && (time.getTime() > vueIns.endDate.getTime()))  
							        }  
							    },
		    					isA:false,
		    					isfA:false,
		    					modaldel:false,
		    					modalTotal:false,
		    					pageIndex:1,
		    					pageSize:12,
		    					totalPage:null,
		    					totalNum:null,
		    					keyword:null,
		    					submitType:null,
		    					canEdit:true,
		    					
		    					staDate:null,
		    					endDate:null,
		    					
		    					noticeMsg: {
				                    BIAOTI:null,
				                    NEIRONG:null,
				                    FABUREN:null,
				                    FABU_SJ:null,
				              },
				                updateMsg:{},
				                ruleInline: {
					                    BIAOTI: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                    FABUREN: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],
					                    /*FABU_SJ: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],*/
					                    NEIRONG: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ]
				                	},
								}
		    	},
		    	computed:{
		    		changeP:function(){
		    			console.log(this.$store.state.placeId);
		    			this.$store.state.placeId="1101001"
						this.reloadTable();
		    			return this.$store.state.placeId;
		    		},
		    	},
		    	updated(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	}, 
		    	created(){
		    		vueIns=this;
		    	},
		    	methods:{
		    		check_box:function(){
		    			this.isA = !this.isA;
		    			this.isfA = !this.isfA;
		    			if(this.isA){
		    				this.$store.state.tableselectCode=this.$store.state.tableallCode;
		    			}else{
		    				this.$store.state.tableselectCode=[];
		    			}
		    			console.log(this.$store.state.tableselectCode);
		    		},
		    		searchMsg(){
		    	
		    			this.reloadTable();
		    		},
		    		delNotice(){
		    			this.modaldel = false;
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    			}else{
		    				console.log(this.$store.state.tableselectCode.join(","));
		    				
		    					this.$http.get(config.content+'/asmx/GongGaoService.asmx/DeleteGongGaoByID',{params:{key:this.$store.state.tableselectCode.join(",")}}).then(response => {
						 								
										this.$Notice.success({
						                    title: '',
						               });
										this.reloadTable();
						        }, response => {
						            console.log("error");
						        });
		    			}
		    		},
		    		 handleSubmit(name) {
		                this.$refs[name].validate((valid) => {
		                    if (valid) {
		                    	let jsons=this.noticeMsg;
		                    	jsons.PARENTID=this.$store.state.placeId;
		                    	jsons=JSON.stringify(jsons);
		                    	
		                    	console.log(jsons);
		                    	if(this.submitType =="add"){
		                    		this.$http.get(config.content+'/asmx/GongGaoService.asmx/AddGongGao',{params:{
		                    			json:jsons
			                    	}}).then(response => {
											this.$Message.success('!');
			                        		this.reloadTable();
			                        		this.$Notice.success({
							                    title: '',
							               	});
							               this.modalTotal = false;
							        }, response => {
							            console.log("error");
							        });
		                    	}else{
		                    		this.$http.get(config.content+'/asmx/GongGaoService.asmx/UpdateGongGao',{params:{
		                    			json:jsons
			                    	}}).then(response => {
											this.$Message.success('!');
			                        		this.reloadTable();
			                        		this.$Notice.success({
							                    title: '',
							               	});
							               this.modalTotal = false;
							        }, response => {
							            console.log("error");
							        });
		                    	}
		                    	
		                    	
		                    } else {
		                        this.$Message.error('!');
		                    }
		                })
		            },
		            openDel(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    			}else{
		    				this.modaldel = true;
		    			}
		    		},
		    		closeModal(){
		    			this.modalTotal = false;
		    				this.modaldel = false;
		    				this.$Message.info('');
		    		},
		    		getTodo(data){
		    			this.updateMsg=data;
		    			console.log(data);
		    		},
		    		addNotice(){
		    			this.submitType ="add";
		    			this.modalTotal=true;
		    		},
		    		updateNotice(){
		    			this.submitType ="update";
		    			this.noticeMsg=this.updateMsg;
		    			this.modalTotal=true;
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		},
		    		reizeDate(data){
		    			const d=new Date(data);
		    			return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();  
		    		},
		    		
		    		reloadTable(){
		    					console.log(this.keyword);
		    			let Index=this.pageIndex-1;
		    			let [st,et]=[this.staDate ? this.reizeDate(this.staDate):null,this.endDate ? this.reizeDate(this.endDate):null]
		    			console.log(this.reizeDate(this.endDate));
		    			this.$http.get(config.content+'/asmx/GongGaoService.asmx/GetGongGaoDataByPage',{params:{PARENTID:this.$store.state.placeId,staDate:st,endDate:et,pageSize:this.pageSize,pageIndex:Index,keyword:this.keyword}}).then(response => {
						 								
										let result = response.body;
						                let father=[];
						                 result = $.parseXML(result);
						                 let obj;
						                 $(result).find("string").each(function(i){                     
						                 	obj=$.parseJSON($(this).text());                      
						                })
						                 console.log(obj);
						                 this.tr=obj.Result.TableInfo;
						                 this.$store.state.tableallCode=[];
						                 this.$store.state.tableselectCode=[];
						                 
						                 for(var i=0;i<this.tr.length;i++){
						                 	this.$store.state.tableallCode.push(this.tr[i].ID);
						                 }
						                 
						                 this.totalNum=obj.Result.RCount;
						                 this.totalPage=obj.Result.PCount;
										
						        }, response => {
						            console.log("error");
						        });
		    		},
		    		//
		    		nextPage(){
		    			if( this.pageIndex>this.totalPage-1){
		    				this.$Message.error('');
		    				this.pageIndex=this.totalPage;
		    			}else{
		    				this.pageIndex++;
		    				this.reloadTable();
		    			}
		    		},
		    		prevPage(){
		    			
		    			if( this.pageIndex<2){
		    				this.$Message.error('');
		    				this.pageIndex=1;
		    			}else{
		    				this.pageIndex--;
		    				this.reloadTable();
		    			}
		    		},
		    		firstPage(){
		    			this.pageIndex=1;
		    			this.reloadTable();
		    		},
		    		lastPage(){
		    			this.pageIndex=this.totalPage;
		    			this.reloadTable();
		    		},
		    		GOPage(){
		    			this.reloadTable();
		    		}
		    	},
}
</script>

<style scoped="scoped">
/********************/
.main .table{
	margin-bottom:0px;
}
.main div.search{
	color:#fff;
	height:2.5em;
	padding-right:0.5em;
}
.main div.search>div{
	color:#fff;
	float:right;
	width:4.5em;
	height:2em;
	border-radius: 5px;
	background-color: rgba(205,210,234,0.2);
	margin-left:10px;
	line-height: 2em;
	cursor: pointer;
	border:1px solid #8a92a8;
}
.main div.search>div.date{
	float:left;
	width:34em;
	background: none;
	border:none;
}
.main div.search>div.input{
	width:18em;
	position:relative;
	border:none;
	background: none;
	margin-top:-4px;
}

.main div.search>div.input>span{
	width:6em;
	display: inline-block;
}
.main div.search .ivu-select-selection{
	background: none !important;
	box-shadow: none !important;
}

.main div.search input::-webkit-input-placeholder {
  color: #fff;
}
.main div.search input:-moz-placeholder {/* Firefox 18- */
  color: #fff;
}
.main div.search input::-moz-placeholder{/* Firefox 19+ */
 color: #fff;
}
.main div.search input:-ms-input-placeholder {
  color: #fff;
}
.main div.search input:-o-input-placeholder {
  color: #fff;
}
.main div.search div>span{
	display: block;
	float:left;
	height:22px;
	width:22px;
	margin-top:5px;
	margin-left:5px;
}
.main div.search div.del:hover{
	padding-top:2px;
}
.main div.search div.add:hover{
	padding-top:2px;
}
.main div.search div.update:hover{
	padding-top:2px;
}
.main div.search div.del>span{
	background-image: url(../assets/Manage/del.png);
}
.main div.search div.add>span{
	background-image: url(../assets/Manage/add.png);
}
.main div.search div.update>span{
	background-image: url(../assets/Manage/update.png);
}
/************************/
.main div.table{
	height: 600px;
}

</style>


