<template>
	<div class="main">
		
		<div class="local">
		<span class="pos1"></span> > <span class="pos2">{{ $route.name}}</span>
	</div>
	<div class="search">
		    <div class="del" @click="openDel"><span></span></div>
		    <div class="update" @click="updateMap"><span></span></div>
		    <div class="add" @click="addMap"><span></span></div>
		    <div class="input">
			    <span></span>
				<Select class="input" v-model="keyword" @change="searchMsg" style="width:200px;background:none">
					  <Option value=""></Option>
					  <Option value="Geometry"></Option>
                      <Option value="imageLayer"></Option>
                      <Option value="baseLayer"></Option>
                      <Option value="demLayer">DEM</Option>
                      <Option value="businessLayer"></Option>
                      <Option value="featureServer"></Option>
                      <Option value="themeLayer"></Option>
	    		</Select>
			</div>
	</div>
	<Modal v-model="modaldel" >
         <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span></span>
        </p>
        <p></p>
        <div slot="footer">
        	<Button type="primary"  @click="closeModal"></Button>
            <Button type="error"  @click="delMap"></Button>
        </div>
    </Modal>
    <Modal
        v-model="modalTotal" @on-cancel="closeModal">
         <div slot="header" style="color:#57a3f3;text-align:left;font-size:20px;
		         	font-weight:600">
		            <span</span>
		</div>
        
         <Form ref="formInline" :model="mapMsg" :rules="ruleInline" :label-width="140">
         	<FormItem prop="SERVICE_MC" label="">
	            <Input type="text" v-model="mapMsg.SERVICE_MC" placeholder="">
	            </Input>
	        </FormItem>
         	 <FormItem prop="SERVICE_URL" label="">
	            <Input type="text" v-model="mapMsg.SERVICE_URL" placeholder="">
	               <!-- <Icon type="ios-person-outline" slot="prepend"></Icon>-->
	            </Input>
	        </FormItem>
	        <FormItem label="" prop="SERVICE_LX">
	        	<Select v-model="mapMsg.SERVICE_LX" placeholder="">
	                  <Option value="Geometry"></Option>
                      <Option value="imageLayer"></Option>
                      <Option value="baseLayer"></Option>
                      <Option value="demLayer">DEM</Option>
                      <Option value="businessLayer"></Option>
                      <Option value="featureServer"></Option>
                      <Option value="themeLayer"></Option>
            </Select>
	        </FormItem>
	         <FormItem label="" prop="IS_VALID">
	            <RadioGroup v-model="mapMsg.IS_VALID">
	                <Radio value="0" label=""></Radio>
			        <Radio value="1" label=""></Radio>
	            </RadioGroup>
        	</FormItem>
	        <FormItem prop="MIAOSHU" label="">
	            <Input type="text" v-model="mapMsg.MIAOSHU" placeholder="">
	            </Input>
	        </FormItem>
   		 </Form>
   		  <div slot="footer">
	        	<Button type="text" @click="closeModal"></Button>
	            <Button type="primary" @click="handleSubmit('formInline')"></Button>
        	</div>
    </Modal>
	<div class="page_list">
		<div class="table">
			<table class="msg_table">
			            				<thead>
			            					<tr>
			            						<td>
			            							<div class="check_box" v-bind:class='{ "active" : this.isA, "": !this.isA }' v-on:click="check_box()">
			            							</div>
			            						</td>
			            						<td v-for="i in thead">{{i}}</td>
			            					</tr>
			            				</thead>
			            				<tbody :view-id="changeP">
			            					<ttboady @sendTodo="getTodo" class="MapTable" v-on:change_AC="change_fAC" v-bind:isActive="isfA"  v-for="(item,index) in tr" v-bind:todo="item" v-bind:index="index" v-bind:key="item.id"></ttboady>
			            				</tbody>
			            		</table>
		</div>
		
			            				<ul class="page">
			            					<li><span class="total_page">{{totalPage}}</span>
			            						<span class="page_num">{{pageSize}}</span>
			            						<span class="total_num">{{totalNum}}</span>
			            					</li>
			            					<li>
			            						<div class="top" @click="firstPage"></div>
			            					</li>
			            					<li><div class="prev" @click="prevPage"></div></li>
			            					<li>
			            						<input type="text" v-model="pageIndex" class="local_page"/>
			            					</li>
			            					<li><div class="next" @click="nextPage"></div></li>
			            					<li><div class="bottom" @click="lastPage"></div></li>
			            					<li><div class="gon" @click="GOPage">GO</div></li>
			            				</ul></div>
	</div>
</template>

<script>
 import ttboady from './page/comments/table-tbody'
 import config from '../config/config.js'
	export default{
  components:{ttboady},
	data (){
		    			return {
		    					thead:["","","URL","","",""],
		    					tr:[
									
									], 
		    					isA:false,
		    					isfA:false,
		    					modaldel:false,
		    					modalTotal:false,
		    					pageIndex:1,
		    					pageSize:12,
		    					totalPage:null,
		    					totalNum:null,
		    					keyword:null,
		    					submitType:null,
		    					mapMsg: {
				                    SERVICE_URL:null,
				                    MIAOSHU:null,
				                    SERVICE_MC:null,
				                    SERVICE_LX:null,
				                    IS_VALID:"",
				                },
				                SERVICE_LX:{
				                	Geometry:"",
				                	imageLayer:"",
				                	baseLayer:"",
				                	demLayer:"DEM",
				                	businessLayer:"",
				                	featureServer:"",
				                	themeLayer:"",
				                },
				                updateMsg:{},
				                ruleInline: {
					                    SERVICE_MC: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                     SERVICE_URL: [
					                        { required: true, message: '', trigger: 'blur' }
					                    ],
					                    SERVICE_LX: [
					                        { required: true, message: '', trigger: 'blur' },
					                    ],
					                    IS_VALID: [
					                        { required: true, message: '/', trigger: 'blur' },
					                    ]
				                	}
		    					
								}
		    },
		    	computed:{
		    		changeP:function(){
		    			console.log(this.$store.state.placeId);
		    			this.$store.state.placeId="1101001"
						this.reloadTable();
		    			return this.$store.state.placeId;
		    		},
		    	},
		    	updated(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    	},
		    	mounted(){
		    		$("table.msg_table tr:odd").addClass("odd");
		    		//swal("", "", "error");
		    		/* layer.alert('style', {
						    skin: 'layui-layer-lan'
						    ,closeBtn: 0
						    ,anim: 0 //
						  });*/
		    	},
		    	methods:{
		    		check_box:function(){
		    			this.isA = !this.isA;
		    			this.isfA = !this.isfA;
		    			if(this.isA){
		    				this.$store.state.tableselectCode=this.$store.state.tableallCode;
		    			}else{
		    				this.$store.state.tableselectCode=[];
		    			}
		    			console.log(this.$store.state.tableselectCode);
		    		},
		    		searchMsg(){
		    			this.reloadTable();
		    		},
		    		openDel(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    			}else{
		    				this.modaldel = true;
		    			}
		    		},
		    		delMap(){
						this.modaldel = false;
		    			this.$http.get(config.content+'/asmx/MapService.asmx/DeleteMapService',{params:{key:this.$store.state.tableselectCode.join(",")}}).then(response => {				
										this.$Notice.success({
						                    title: '',
						               });
										this.reloadTable();
						        }, response => {
						             swal("", "", "error");
						        });
		    		},
		    		 handleSubmit(name) {
		                this.$refs[name].validate((valid) => {
		                    if (valid) {
		                    	let jsons=this.mapMsg;
		                    	jsons.PARENT_ID=this.$store.state.placeId;
		                    	
		                    	if(jsons.IS_VALID==""){
		                    		jsons.IS_VALID="0";
		                    	}else if(jsons.IS_VALID==""){
		                    		jsons.IS_VALID="1";
		                    	}
		                    	
		                    	jsons=JSON.stringify(jsons);
		                    	console.log(jsons);
		                    	
		                    	if(this.submitType =="add"){
		                    		this.$http.get(config.content+'/asmx/MapService.asmx/AddMapService',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										
										this.$Message.success('');
			                        	this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							             swal("", "", "error");
							        });
		                    	}else{
		                    		this.$http.get(config.content+'/asmx/MapService.asmx/UpdateMapService ',{params:{
		                    			json:jsons
			                    	}}).then(response => {
										   this.$Message.success('');
			                        	   this.reloadTable();
							               this.modalTotal = false;
							               this.$Notice.success({
							                    title: '',
							               	});
							        }, response => {
							    
							            swal("", "", "error");
							        });
		                    	}
		                    	
		                    	
		                    } else {
		                        this.$Message.error('');
		                    }
		                })
		            },
		    		closeModal(){
		    			this.modalTotal = false;
		    				this.modaldel = false;
		    				
		    				this.$Message.info('');
		    				this.reloadTable();
		    		},
		    		getTodo(data){
		    			this.updateMsg=data;
		    		},
		    		addMap(){
		    			this.submitType ="add";
		    			this.mapMsg={
				                    SERVICE_URL:null,
				                    MIAOSHU:null,
				                    SERVICE_MC:null,
				                    SERVICE_LX:null,
				                    IS_VALID:"",
				                },
		    			this.modalTotal=true;
		    		},
		    		updateMap(){
		    			if(this.$store.state.tableselectCode.length<1){
		    				this.$Message.warning('');
		    				//swal("", "", "warning");
		    			}else{
		    				this.submitType ="update";
			    			this.mapMsg=this.updateMsg;
			    			
			    			console.log(this.mapMsg);
			    			
			    			this.modalTotal=true;
		    			}
		    			
		    		},
		    		change_fAC(data){
		    			console.log(data);
		    			this.isfA = data;
		    		},
		    		reloadTable(){
		    					console.log(this.keyword);
		    			let Index=this.pageIndex-1;
		    			this.$http.get(config.content+'/asmx/MapService.asmx/GetMapServiceData',{params:{PARENTID:this.$store.state.placeId,pageSize:this.pageSize,pageIndex:Index,keyword:this.keyword}}).then(response => {
						 								
										let result = response.body;
						                let father=[];
						                 result = $.parseXML(result);
						                 let obj;
						                 $(result).find("string").each(function(i){                     
						                 	obj=$.parseJSON($(this).text());                      
						                })
						                 
						                 console.log(obj);
						                 
						                 for(var i=0;i<obj.Result.TableInfo.length;i++){
						                 	obj.Result.TableInfo[i].SERVICE_LX_E=obj.Result.TableInfo[i].SERVICE_LX;
						                 	obj.Result.TableInfo[i].SERVICE_LX=this.SERVICE_LX[obj.Result.TableInfo[i].SERVICE_LX];
						                 	
						                 	if(obj.Result.TableInfo[i].IS_VALID=="0"){
						                 		obj.Result.TableInfo[i].IS_VALID="";
						                 	}else if(obj.Result.TableInfo[i].IS_VALID=="1"){
						                 		obj.Result.TableInfo[i].IS_VALID="";
						                 	}
						                 }
						                 
						                 
						                 this.tr=obj.Result.TableInfo;
						                 
						                 
						                 this.$store.state.tableallCode=[];
						                 this.$store.state.tableselectCode=[];
						                 for(var i=0;i<this.tr.length;i++){
						                 	this.$store.state.tableallCode.push(this.tr[i].ID);
						                 }
						                 
						                 	this.totalNum=obj.Result.RCount;
						                 	this.totalPage=obj.Result.PCount;
										
						        }, response => {
						            console.log("error");
						        });
		    		},
		    		//
		    		nextPage(){
		    			if( this.pageIndex>this.totalPage-1){
		    				
		    				this.$Message.error('');
		    				this.pageIndex=this.totalPage;
		    			}else{
		    				this.pageIndex++;
		    				this.reloadTable();
		    			}
		    		},
		    		prevPage(){
		    			
		    			if( this.pageIndex<2){
		    				this.$Message.error('');
		    				this.pageIndex=1;
		    			}else{
		    				this.pageIndex--;
		    				this.reloadTable();
		    			}
		    		},
		    		firstPage(){
		    			this.pageIndex=1;
		    			this.reloadTable();
		    		},
		    		lastPage(){
		    			this.pageIndex=this.totalPage;
		    			this.reloadTable();
		    		},
		    		GOPage(){
		    			this.reloadTable();
		    		}
		    	},
}
</script>

<style scoped="scoped">
	

/********************/
.main .table{
	margin-bottom:0px;
}
.main div.search{
	color:#fff;
	height:2.5em;
	padding-right:0.5em;
}
.main div.search>div{
	color:#fff;
	float:right;
	width:4.5em;
	height:2em;
	border-radius: 5px;
	background-color: rgba(205,210,234,0.2);
	margin-left:10px;
	line-height: 2em;
	cursor: pointer;
	border:1px solid #8a92a8;
}
.main div.search>div.input{
	width:18em;
	position:relative;
	border:none;
	background: none;
	margin-top:-2px;
}
.main div.search>div.input>span{
	width:5em;
	display: inline-block;
}
/*.main div.search input{
	width:22.6em;
	outline: none;
	background: none;
	border:none;
	color:#fff;
	padding-left:1em;
}
.main div.search div#icon{
	position:absolute;
	height:2em;
	width:2em;
	background-image: url(../assets/Manage/search.png);
	background-size: contain;
	top:0px;
	right:0px;
}
.main div.search div#icon:hover{
	top:2px;
}*/

.main div.search input::-webkit-input-placeholder {
  color: #fff;
}
.main div.search input:-moz-placeholder {/* Firefox 18- */
  color: #fff;
}
.main div.search input::-moz-placeholder{/* Firefox 19+ */
 color: #fff;
}
.main div.search input:-ms-input-placeholder {
  color: #fff;
}
.main div.search input:-o-input-placeholder {
  color: #fff;
}
.main div.search div>span{
	display: block;
	float:left;
	height:22px;
	width:22px;
	margin-top:5px;
	margin-left:5px;
}
.main div.search div.del:hover{
	padding-top:2px;
}
.main div.search div.add:hover{
	padding-top:2px;
}
.main div.search div.update:hover{
	padding-top:2px;
}
.main div.search div.del>span{
	background-image: url(../assets/Manage/del.png);
}
.main div.search div.add>span{
	background-image: url(../assets/Manage/add.png);
}
.main div.search div.update>span{
	background-image: url(../assets/Manage/update.png);
}
/************************/
.main div.table{
	height: 600px;
}
</style>


